import React from 'react'
import { Slide, Heading, Notes, List, ListItem } from 'spectacle'

import TextHighlight from '../components/TextHighlight'

function ProvideSimpleVocabulary() {
  return (
    <Slide>
      <Heading size={1} fit caps>
        Fornea um vocabulrio simples,
        <br />
        sempre que possvel, evite o uso de:
      </Heading>

      <List>
        <ListItem>Metforas</ListItem>
        <ListItem>Coloquialismo</ListItem>
        <ListItem>Expresses no literais</ListItem>
        <ListItem>Abreviaes/acrnimos</ListItem>
      </List>

      <Notes>
        Usar uma linguagem que <TextHighlight>foge do padro</TextHighlight> pode
        confundir/estressar o usurio com <TextHighlight>autismo</TextHighlight>
        <br />
        <br />
        Se usar <TextHighlight>abreviaes</TextHighlight> ou{' '}
        <TextHighlight>acrnimos</TextHighlight> fornea o significado da sigla ou da abreviao
      </Notes>
    </Slide>
  )
}

export default ProvideSimpleVocabulary
