import React from 'react'
import { Heading, List, ListItem, Slide, Layout, Fit, Notes, Text, S } from 'spectacle'

import Link from '../components/Link'
import TextHighlight from '../components/TextHighlight'

function ComponentWithAccessibilityIssues() {
  return [
    <Slide>
      <Heading size={1} fit caps>
        Evite usar componentes
        <br />
        conhecidos por m acessibilidade
      </Heading>

      <List>
        <ListItem>Modais/Pop-ups</ListItem>
        <ListItem>Carousel</ListItem>
        <ListItem>Tooltip</ListItem>
      </List>
    </Slide>,

    <Slide contentStyles={{ height: '100%', padding: '40px 0' }}>
      <Layout style={{ height: '100%', flexDirection: 'column', justifyContent: 'center' }}>
        <Fit style={{ paddingBottom: 20 }}>
          <Heading size={1} fit caps>
            Modais geram quebra de
            <br />
            expectativa, de forma negativa
          </Heading>
        </Fit>

        <Fit>
          <video style={{ height: '68vh', maxHeight: 490 }} autoPlay loop>
            <source src='./videos/auto-open-modal.mp4' type='video/mp4' />
          </video>
        </Fit>
      </Layout>

      <Notes>
        Modais que abrem sozinha, como ao entrar numa pgina ou antes de sair, geram uma quebra de
        expectativa e podem ser uma situao de stress para{' '}
        <TextHighlight>quem tem autismo</TextHighlight>
      </Notes>
    </Slide>,

    <Slide contentStyles={{ height: '100%', padding: '40px 0' }}>
      <Layout style={{ height: '100%', flexDirection: 'column', justifyContent: 'center' }}>
        <Fit style={{ paddingBottom: 20 }}>
          <Heading size={1} fit caps>
            A maioria das modais no possuem
            <br />
            controle de foco (focus trap)
          </Heading>
        </Fit>

        <Fit>
          <video style={{ height: '68vh', maxHeight: 490 }} autoPlay loop>
            <source src='./videos/modal-focus-trap.mp4' type='video/mp4' />
          </video>
        </Fit>
      </Layout>
    </Slide>,

    <Slide>
      <Heading size={1} fit caps style={{ paddingBottom: 10 }}>
        Modais no seguem
        <br />o fluxo de contedo do site
      </Heading>

      <Text textColor='secondary'>
        Dependendo do layout, modais pode serem vistas como uma pgina por si s, e usurios com{' '}
        <S type='bold' textColor='tertiary'>
          deficincias cognitivas
        </S>{' '}
        podem tentar apertar o boto de voltar pgina e no ter o resultado esperado
      </Text>
    </Slide>,

    <Slide>
      <Heading size={1} fit caps>
        Problemas de Carousel
      </Heading>

      <List>
        <ListItem>Autoplay  complicado...</ListItem>

        <ListItem>
          A maioria dos plugins no tem uma navegao com teclado decente/suporte para screen
          readers
        </ListItem>

        <ListItem>Contedo de difcil descoberta</ListItem>

        <ListItem>
          <Link href='http://shouldiuseacarousel.com/' target='_blank' textColor='tertiary'>
            shouldiuseacarousel.com
          </Link>
        </ListItem>
      </List>

      <Notes>
        Autoplay sem pause quando t com mouse em cima. Isso pode ser prejudicial para usurios com{' '}
        <TextHighlight>dislexia/deficit de ateno</TextHighlight>.
        <br />
        <br />
        Difcil descoberta de contedo - Impacta <TextHighlight>
          {' '}
          acessibilidade{' '}
        </TextHighlight> e <TextHighlight>engajamento dos usurios</TextHighlight>
      </Notes>
    </Slide>,

    <Slide>
      <Heading size={1} fit caps>
        Problemas de Tooltip
      </Heading>

      <List>
        <ListItem>Dificulta/impossibilita navegao com teclado</ListItem>
        <ListItem>Fraca UX em dispositivos mveis</ListItem>
        <ListItem>S serve para guardar quantidades pequenas de informao</ListItem>
        <ListItem>No deve ser usada para informaes cruciais pro usurio</ListItem>
      </List>
    </Slide>
  ]
}

export default ComponentWithAccessibilityIssues
