import React from 'react'
import { Slide, Heading, Appear, Quote, Cite, BlockQuote, Notes, Text } from 'spectacle'

function MinorityUserMyth() {
  return [
    <Slide>
      <Heading size={1} fit caps>
        Mito do usurio de minoria
      </Heading>

      <Appear>
        <BlockQuote style={{ marginTop: 80 }}>
          <Quote textSize={50} textColor='secondary' style={{ borderColor: 'white' }}>
            Acessibilidade  mais uma coisa pra mim me preocupar
          </Quote>
          <Cite margin={20}>Algum que precisa assistir esse dojo</Cite>
        </BlockQuote>
      </Appear>

      <Appear>
        <BlockQuote style={{ marginTop: 20 }}>
          <Quote textSize={50} textColor='secondary' style={{ borderColor: 'white' }}>
            Quase ningum  beneficiado com isso...
          </Quote>
          <Cite margin={20}>O mesmo cara da frase de cima</Cite>
        </BlockQuote>
      </Appear>

      <Notes> um argumento fcil de utilizar e nos livra de se preocupar com vrias coisas</Notes>
    </Slide>,

    <Slide>
      <Heading size={1} fit caps style={{ paddingBottom: 10 }}>
        O quanto  "quase ningum"?
      </Heading>

      <Text textColor='secondary'>
        1 a cada 7 pessoas no mundo possuem algum tipo de deficincia, em algum nvel, que pode
        prejudicar sua experincia ao acessar um site
      </Text>

      <Notes>
        Imagine a cada 7 pessoas, uma delas ser privada de ter uma experincia como todas as outras,
        por algo que ela no escolheu ter
        <br />
        <br />
        Sim, ainda  uma minoria, mas  um nmero significativo para prestar ateno
      </Notes>
    </Slide>
  ]
}

export default MinorityUserMyth
