import React from 'react'
import { Slide, Heading, Text, S, Notes } from 'spectacle'

function SmoothScroll() {
  return (
    <Slide>
      <Heading size={1} fit caps>
        Cuidado com o scroll animado
      </Heading>

      <Text textColor='secondary' textAlign='left' style={{ paddingTop: 30 }}>
        Um scroll animado por muito tempo pode ser desagradvel para pessoas com{' '}
        <S textColor='tertiary' type='bold'>
          epilepsia
        </S>
        .
        <br />
        <br />A durao mxima deve ser de 250ms (1/4 de segundo).
      </Text>

      <Notes>
        Faa uma animao rpida, ou no use animao.
        <br />
        <br />
        250ms  o tempo definido pela a WebAIM
      </Notes>
    </Slide>
  )
}

export default SmoothScroll
