import React from 'react'
import { Slide, Heading, Notes, Text, S } from 'spectacle'

function Publics() {
  return [
    <Slide>
      <Heading size={1} fit caps>
        Os 4 pblicos que so mais impactados
        <br />
        pela acessibilidade (ou a falta dela)
      </Heading>

      <Text textColor='secondary' style={{ paddingTop: 40 }}>
        Usurios com algum nvel de deficincia{' '}
        <S type='bold' textColor='tertiary'>
          visual
        </S>
        ,{' '}
        <S type='bold' textColor='tertiary'>
          motora
        </S>
        ,{' '}
        <S type='bold' textColor='tertiary'>
          auditiva
        </S>{' '}
        ou{' '}
        <S type='bold' textColor='tertiary'>
          cognitiva
        </S>
      </Text>

      <Notes>
        <S type='bold' textColor='tertiary'>
          Visuais
        </S>
        : miopia, cegueira, daltonismo
        <br />
        <S type='bold' textColor='tertiary'>
          Motoras
        </S>
        : leso por esforo repetitivo
        <br />
        <S type='bold' textColor='tertiary'>
          Cognitivas
        </S>
        : autismo, dficit de ateno, dislexia, ansiedade
        <br />
        <br />A acessibilidade no necessariamente  pensada em atender s esses grupos, mas so
        eles que so mais beneficiados com isso
      </Notes>
    </Slide>
  ]
}

export default Publics
