import React from 'react'
import { Slide, Notes, Heading, Text, Layout, Appear, Image, List, ListItem } from 'spectacle'
import TextHighlight from '../components/TextHighlight'

function Principles() {
  return [
    <Slide>
      <Heading size={1} fit caps>
        Os 4 princpios da acessibilidade (POUR)
      </Heading>

      <List>
        <ListItem>Perceivable (perceptvel)</ListItem>
        <ListItem>Operable (opervel)</ListItem>
        <ListItem>Understandable (coerente)</ListItem>
        <ListItem>Robust (robusto)</ListItem>
      </List>

      <Notes>O que minha aplicao precisa ter pra ser considerada acessvel?</Notes>
    </Slide>,

    <Slide>
      <Heading size={1} fit caps>
        Perceivable (perceptvel)
      </Heading>

      <Text fit textColor='secondary'>
        Cada usurio tem capacidades e limitaes diferentes
      </Text>

      <Layout style={{ marginTop: 20 }}>
        <Appear>
          <Image style={{ maxWidth: '33%' }} margin={5} src='./images/braille-reader.jpg' />
        </Appear>

        <Appear>
          <Image style={{ maxWidth: '33%' }} margin={5} src='./images/eye-tracker.png' />
        </Appear>

        <Appear>
          <Image style={{ maxWidth: '33%' }} margin={5} src='./images/switch-access.jpg' />
        </Appear>
      </Layout>

      <Notes>
        Ter em mente que s por que o contedo  visvel{' '}
        <TextHighlight>com um sentido</TextHighlight> (como a viso), no quer dizer que{' '}
        <TextHighlight>todos consigam consumir</TextHighlight>
        <br />
        <br />O contedo do site deve ser perceptvel de{' '}
        <TextHighlight>todas as formas possveis</TextHighlight>
      </Notes>
    </Slide>,

    <Slide>
      <Heading size={1} fit caps>
        Operable (opervel)
      </Heading>

      <Text fit textColor='secondary'>
        Permitir que o usurio consiga operar
        <br />o site das mais diversas formas
      </Text>

      <Notes>
        Como usando somente o <TextHighlight>teclado</TextHighlight> ou{' '}
        <TextHighlight>mouse</TextHighlight>
        <br />
        <br />
        Fazer com que a experincia no site seja a mesma,{' '}
        <TextHighlight>independente do dispositivo usado</TextHighlight>
      </Notes>
    </Slide>,

    <Slide>
      <Heading size={1} fit caps>
        Understandable (coerente)
      </Heading>

      <Text fit textColor='secondary'>
        Tanto a interface quanto o contedo
        <br />
        dela devem ser de fcil entendimento
      </Text>
    </Slide>,

    <Slide>
      <Heading size={1} fit caps>
        Robust (robusto)
      </Heading>

      <Text fit textColor='secondary'>
        Permitir que o contedo seja acessado numa
        <br />
        grande variedade de browsers e dispositivos
      </Text>
    </Slide>
  ]
}

export default Principles
