import React from 'react'
import { Slide, Heading, Notes } from 'spectacle'

import FullImage from '../components/FullImage'
import TextHighlight from '../components/TextHighlight'
import ImageWithCredits from '../components/ImageWithCredits'

function KeyboardNavigation() {
  return [
    <Slide>
      <Heading size={1} fit caps>
        Garanta que todo contedo <br />
        possa ser acessado com teclado
      </Heading>

      <Notes>
        Ajuda no s pessoas com <TextHighlight>deficincias motoras</TextHighlight>, mas tambm{' '}
        <TextHighlight>powerusers</TextHighlight> que tem costume de navegar com o teclado, o que 
        bem comum
      </Notes>
    </Slide>,

    <Slide>
      <Heading size={1} fit caps>
        Scroll infinito atrapalha
        <br />
        navegao por teclado
      </Heading>
    </Slide>,

    <Slide progressColor='transparent'>
      <FullImage src='./images/medium-footer.jpg' style={{ objectPosition: 'top center' }} />

      <Notes>
        A home do medium no tem como focar com o teclado nessas informaes  direita por que tem{' '}
        <TextHighlight>scroll infinito</TextHighlight>
      </Notes>
    </Slide>,

    <Slide>
      <Heading size={1} fit caps>
        No esquea do estilo de foco
      </Heading>

      <Notes>
        Certifique-se que seja <TextHighlight>fcil de perceber</TextHighlight> quando qualquer
        elemento na tela est sendo focado
      </Notes>
    </Slide>,

    <Slide>
      <ImageWithCredits
        author='Zell'
        credits='Designing a focus style'
        src='./images/twitter-focus-management.gif'
        creditsLink='https://zellwk.com/blog/design-focus-style/'
      />
    </Slide>,

    <Slide>
      <ImageWithCredits
        author='Zell'
        credits='Designing a focus style'
        src='./images/bad-focus-style.gif'
        creditsLink='https://zellwk.com/blog/design-focus-style/'
      />
      <Notes>
        Um usurio daltnico pode (e vai) ter dificuldade de{' '}
        <TextHighlight>ver que o texto est com foco</TextHighlight> caso ele esteja navegando pelo
        teclado
      </Notes>
    </Slide>
  ]
}

export default KeyboardNavigation
