import React from 'react'
import { Slide, Heading, Text, Notes } from 'spectacle'

import TextHighlight from '../components/TextHighlight'

// import MetaViewport from '../components/MetaViewport'

const ICON_WIDTH = 37

function DontDisableZoom() {
  function renderText(text, icon = './images/plus-icon.svg') {
    return (
      <Text textColor='secondary' textAlign='left' style={{ paddingBottom: 10 }}>
        <img
          alt=''
          src={icon}
          width={ICON_WIDTH}
          style={{
            display: 'inline-block',
            verticalAlign: -3,
            marginRight: 10
          }}
        />
        {text}
      </Text>
    )
  }

  function renderPlusText(text) {
    return renderText(text, './images/plus-icon.svg')
  }

  function renderLessText(text) {
    return renderText(text, './images/less-icon.svg')
  }

  return [
    <Slide>
      <Heading size={1} textSize={55} caps>
        No tire o poder do usurio decidir o tamanho das coisas na tela (zoom)
      </Heading>

      {/* <MetaViewport /> */}
    </Slide>,

    <Slide>
      <Heading size={1} fit caps>
        Tudo comeou quando
        <br />o design responsivo foi adotado
      </Heading>

      <Notes>
        Ficou comum a prtica de <TextHighlight>desativar o zoom</TextHighlight>
        <br />
        <br />
        Afinal, <TextHighlight>tudo est do tamanho desejado para todo mundo</TextHighlight>, certo?
        Errado.
      </Notes>
    </Slide>,

    <Slide>
      <Heading size={1} fit caps>
        Escolhas que podemos
        <br />
        fazer em relao ao zoom
      </Heading>

      <Notes>Temos 3 escolhas que podemos fazer em relao  isso</Notes>
    </Slide>,

    <Slide>
      <Heading size={1} fit caps style={{ paddingBottom: 10 }}>
        Usar fontes extra-grandes para tudo
      </Heading>

      {renderPlusText('Beneficia usurios com deficincias visuais')}
      {renderLessText('Prejudica a esttica do site')}
      {renderLessText('Limita a quantidade de contedo na tela')}

      <Notes>
        Essa  uma <TextHighlight>estratgia inclusiva</TextHighlight>, mas talvez atrapalhe quem
        no tenha esse tipo de necessidade
      </Notes>
    </Slide>,

    <Slide>
      <Heading size={1} fit caps style={{ paddingBottom: 10 }}>
        Fazer design s para a maioria
      </Heading>

      {renderLessText('Aceitar que meu site no  inclusivo')}

      <Notes>
        Estratgia <TextHighlight>cmoda</TextHighlight> e <TextHighlight>exclusiva</TextHighlight>
      </Notes>
    </Slide>,

    <Slide>
      <Heading size={1} fit caps style={{ paddingBottom: 10 }}>
        No desabilitar o zoom
      </Heading>

      {renderPlusText('Liberdade para o usurio')}
      {renderLessText('Ao custo de alguns gestos de pinch')}

      <Notes>
        Estratgia <TextHighlight>inclusiva</TextHighlight>
      </Notes>
    </Slide>
  ]
}

export default DontDisableZoom
