import React from 'react'
import { Slide, Heading, Notes } from 'spectacle'

import TextHighlight from '../components/TextHighlight'

function PredeterminatedActionTime() {
  return (
    <Slide>
      <Heading size={1} fit caps>
        No d um tempo determinado
        <br />
        para o usurio finalizar uma tarefa
      </Heading>

      <Notes>
        Por mais que a gente tente, no temos como prever todos os tipos de{' '}
        <TextHighlight>dificuldades</TextHighlight> que o usurio pode ter na nossa aplicao.
      </Notes>
    </Slide>
  )
}

export default PredeterminatedActionTime
