import React from 'react'
import {
  Slide,
  Heading,
  Notes,
  Layout,
  Link,
  BlockQuote,
  Quote,
  Cite,
  Text,
  Fill,
  ListItem,
  List,
  Appear
} from 'spectacle'

import TextHighlight from '../components/TextHighlight'

function Conclusion() {
  return [
    <Slide>
      <Heading size={1} caps textSize={56}>
        Checklist
      </Heading>

      <List>
        <ListItem>Garanta uma boa navegao por teclado</ListItem>
        <Appear>
          <ListItem>Permita que o usurio d zoom</ListItem>
        </Appear>
        <Appear>
          <ListItem>Fornea alternativas  contedos no textuais</ListItem>
        </Appear>
        <Appear>
          <ListItem>Garanta um bom contraste de cores</ListItem>
        </Appear>
        <Appear>
          <ListItem>Provenha tamanhos de fontes adequados</ListItem>
        </Appear>
        <Appear>
          <ListItem>Use uma linguagem simples</ListItem>
        </Appear>
        <Appear>
          <ListItem>No use a cor como nico indicativo de informao</ListItem>
        </Appear>
      </List>
    </Slide>,

    <Slide>
      <Heading size={1} caps textSize={56}>
        Concluindo
      </Heading>

      <Notes>
        Preciso seguir <TextHighlight>todas as dicas</TextHighlight>?
        <br />
        <br />
        Acessibilidade = <TextHighlight>prima-irm da UX</TextHighlight>
        <br />
        <br />A Web foi feita pra ser acessvel <TextHighlight>desde o comeo</TextHighlight>
      </Notes>
    </Slide>,

    <Slide>
      <BlockQuote>
        <Quote textSize={50} textColor='secondary' style={{ borderColor: 'white' }}>
          The power of the Web is in its universality. Access by everyone regardless of disability
          is an essential aspect
        </Quote>
        <Cite margin={20}>Tim Berners-Lee</Cite>
      </BlockQuote>

      <Notes>
        <TextHighlight>NO ESQUEA DE ABRIR ESPAO PARA PERGUNTAS</TextHighlight>
      </Notes>
    </Slide>,

    <Slide>
      <Layout style={{ justifyContent: 'center' }}>
        <Fill>
          <Heading size={1} caps textSize={56} style={{ paddingBottom: 20 }}>
            Obrigado!
          </Heading>

          <Text textColor='secondary'>Caso queira deixar seu feedback:</Text>

          {/* <Text
              textColor='secondary'
              style={{
                display: 'flex',
                alignItems: 'center',
                justifyContent: 'center',
                padding: 5
              }}
            >
              <img width={40} style={{ marginRight: 10 }} src='./images/slack.svg' alt='Slack' />
              michell.santo
            </Text> */}

          <Link
            href='mailto:michell.santo@cwi.com.br'
            textColor='tertiary'
            style={{
              display: 'flex',
              alignItems: 'center',
              justifyContent: 'center',
              padding: 5,
              marginTop: 30
            }}
          >
            <img width={40} style={{ marginRight: 10 }} src='./images/mail.svg' alt='E-mail' />
            michell.santo@cwi.com.br
          </Link>
        </Fill>
      </Layout>
    </Slide>
  ]
}

export default Conclusion
